<template>
  <div v-if="!registerSuccessful" class="register">
    <div class="container">
      <div class="image">
        <Logo/>
        <img class='imagen' src="@/assets/register-IMG.png" alt="">
      </div>
      <div class="form" @submit.prevent="handleSubmit">
        <h2>¡Bienvenid@!</h2>
        <form action="#">
          <Input
            class="Input"
            label="Nombres"
            :value="name"
            @inputChange="(value) => name = value"
          />
          <Input
            class="Input"
            label="Apellidos"
            :value="lastname"
            @inputChange="(value) => lastname = value"
          />
          <Input
            class="Input"
            label="email"
            input-type="email"
            :value="email"
            @inputChange="(value) => email = value"
          />
          <Input
            class="Input"
            label="Contraseña"
            input-type="password"
            :value="password"
            @inputChange="(value) => password = value"
          />
          <Input
            class="Input"
            label="Confirmar Contraseña"
            input-type="password"
            :value="passwordConfirmation"
            @inputChange="(value) => passwordConfirmation = value"
          />
          <Button btnType="cta" class="btn--primary Button">Ingresar</Button>
        </form>
      </div>
    </div>
  </div>
  <RegisterFinished v-else/>
</template>

<script>
import Button from '@/components/Button.vue';
import Input from '@/components/Input.vue';
import Logo from '@/components/Logo.vue';
import { mapActions, mapState } from 'vuex';
import RegisterFinished from './Register_Finish.vue';

export default {
  name: 'Registro',
  components: {
    Input,
    Button,
    RegisterFinished,
    Logo,
  },
  data() {
    return {
      name: '',
      lastname: '',
      email: '',
      password: '',
      passwordConfirmation: '',
    };
  },
  computed: {
    ...mapState('authStore', {
      registerSuccessful: 'registerSuccessful',
    }),
  },
  methods: {
    ...mapActions({
      register: 'authStore/register',
    }),
    handleSubmit() {
      if (this.passwordConfirmation === this.password) {
        this.register({
          name: this.name,
          lastname: this.lastname,
          email: this.email,
          password: this.password,
          firstTime: true,
        });
      // eslint-disable-next-line
      } else alert('Las contraseñas no son iguales');
    },
  },
};
</script>

<style lang="scss" scoped>
@import '@/assets/styles/main';

.register {
  min-height: 100vh;
  position: relative;
}

.container {
  height: 100vh;
  margin: auto;
  display: grid;
  grid-template-columns: 60% 40%;

  .image, .form {
    align-self: center;
  }

  .image {
    padding: 2em;
    background-color: #F8F8FA;
    height: 100%;
    display: flex;
    flex-flow: column;
    img {
      margin: auto;
    }
  }

  .form {
    padding: 2em;

    h2 {
      font: $text-mdlg $font-primary;
      font-weight: $bold;
      margin: 0 0 2em;
      text-align: center;
    }

    form {
      display: flex;
      flex-flow: column;
      align-items: center;

      .Input {
        width: 70%;
        margin-bottom: 1.25em;
      }

      .Button {
        width: 70%;
        text-align: center;
        margin: 2.5em 0;
      }
    }
  }

}

@media (max-width: 900px) {
  .container {
    display: flex;
    flex-flow: column;
  }

  .container .image {
    background-color: white;
    height: auto;
    width: 100%;

    .imagen {
      display: none;
    }
  }

  .container .form {
    width: 100%;
    margin-top: 1em;
    padding: 0;

    form input {
      padding: 0.5rem;
    }
  }

  @media (max-width: 450px) {
    .container .image { padding: 1em; }

    .container .form form {
      .Input {
      width: 90%;
      }

      .Button {
        width: 90%;
        margin-top: 1em;
      }
    }
  }

}
</style>
